@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
body {
    margin: 0;
    padding: 0;
    font-family: 'Roboto', sans-serif;
}

.App {
    font-family: 'Roboto', sans-serif;
    align-items: center;
}

.container {
    width: 90vw;
    min-width: 40vw;
    margin: 30px;
}

.wrapper {
    padding: 0 20px;
}

.home {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
    /* background-color: wheat; */
    text-align: left;
}

.home p {
    font-size: 18px;
    line-height: 20px;
    line-height: 22px;
}

.home h5 {
    margin-top: 20vh;
    font-size: 1.6rem;
    font-weight: 600;
    width: 60vw;
    line-height: 35px;
    letter-spacing: 1px;
}

.home h5>span {
    font-size: 1.6rem;
    font-weight: 800;
}

@media(max-width:800px) {
    .container {
        width: 100vw;
        min-width: 40vw;
        margin: 0;
    }
    .home h5 {
        width: 90vw;
    }
    .wrapper {
        padding: 10px;
    }
}


/* Header */

header {
    height: 100px;
    /* background-color: tomato; */
}

.inner-header {
    position: relative;
    z-index: 10;
    height: 100px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo a {
    font-weight: 700;
    font-size: 22px;
    text-decoration: none;
    color: black;
}

.menu button {
    border: none;
    background-color: transparent;
    outline: none;
    cursor: pointer;
    font-size: 1rem;
    font-weight: 600;
    color: rgba(0, 0, 0, 0.829);
}

.hambuger-menu {
    display: none;
    z-index: 9;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    position: fixed;
    height: 100%;
    width: 100%;
    background-color: teal;
}

.menu-second-bg-color {
    z-index: -1;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    position: fixed;
    height: 100vh;
    width: 100%;
    background-color: thistle;
}

.menu-layer {
    position: relative;
    background-color: rgb(206, 37, 37);
    height: 100%;
    overflow: hidden;
}

.menu-city-bg {
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    position: absolute;
    height: 100vh;
    width: 100%;
    opacity: 0;
}

.wrapper {
    position: relative;
}

.menu-links {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: left;
    position: relative;
    top: 20vh;
    overflow: hidden;
}

nav {
    display: block;
}

ul {
    padding: 0;
    margin: 0;
}

li {
    list-style: none;
    font-size: 6rem;
    cursor: pointer;
    font-weight: 700;
    position: relative;
    width: 70vw;
}

a {
    color: white;
    text-decoration: none;
}

a:hover {
    color: black;
    font-style: italic;
    transition: 250ms all ease;
}

.locations {
    margin-top: 20vh;
    color: white;
    display: flex;
    justify-content: space-around;
    font-size: 18px;
    font-weight: 600;
    align-items: center;
}

.locations span {
    padding: 7px 17px;
    font-weight: 500;
    color: rgba(255, 255, 255, 0.945);
}

.locations span:hover {
    cursor: pointer;
    background-color: white;
    color: black;
    transition: 250ms all ease;
    border-radius: 16px 2px;
    padding: 8px 48px;
}